#about {
  width: 100%;
  margin-bottom: 40px;
}

.about-me {
  display: flex;
  flex-direction: column;
  margin-top: 60px;
  margin-bottom: 20px;
}

.pic-container {
  position: relative;
  display: flex;
  align-self: center;
  border-radius: 50%;
  border: 10px solid #fce575;
  box-shadow: 0px 1px 4px #ffd447;
  /* box-shadow: 0px 0px 6px #FCE575; */
}

.profile-pic {
  max-height: 250px;
  max-width: 250px;
  border-radius: 50%;
  -webkit-transition: 0.2s ease;
  transition: 0.2s ease;
}

.profile-pic-overlay{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #fce99591;
  color: white;
  font-weight: bold;
  visibility: hidden;
  opacity: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
  -webkit-transition: 0.2s ease;
  transition: 0.2s ease;
  height: 100%;
  width: 100%;
  border-radius: 50%;
}

.pic-container:hover .profile-pic-overlay{
  visibility: visible;
  opacity: 1;     
  text-decoration: none;
}

.pic-container:hover .profile-pic{
  opacity: 0.65;     
}

.play-button{
  -webkit-transition: 0.2s ease;
  transition: 0.2s ease;
  -webkit-transform: translateY(1em);
          transform: translateY(1em);
  /* color: white; */
}

.pic-container:hover .play-button{
  -webkit-transition: 0.2s ease;
  transition: 0.2s ease;
  -webkit-transform: translateY(0);
          transform: translateY(0); 
  cursor: pointer;
}


.play-icon{
  font-size: 9em !important;
  opacity: 0.75;
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
}

.play-icon:active{
  -webkit-transform: scale(1.1);
          transform: scale(1.1)
}

.fab-play {
  display: flex;
  position: fixed !important;
  z-index: 200;
  bottom: 30px;
  right: 30px;
  -webkit-animation-name: slideInUp;
  animation-name: slideInUp;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
}

.about-me-body {
  margin: auto;
  width: 60%;
}

.about-me-text {
  color: rgb(43, 43, 49);
  font-size: 1.3em;
  line-height: 1.5;
  text-shadow: 0px 2px 3px rgba(0, 0, 0, 0.1);
  text-align: justify;
  /* z-index: 12; */
}

.about-highlight {
  position: relative;
  display: inline-block;
  -webkit-transition: color 0.4s ease-in-out;
  transition: color 0.4s ease-in-out;
  text-decoration: none;
  z-index: 3;
}

.about-highlight:hover {
  text-decoration: none;
  color: #000022;
}

.rockets-highlight:hover {
  color: rgb(245, 222, 222);
  text-shadow: 0px 0px 2px rgba(0, 0, 0, 0.5);

}

.about-highlight::after,
.about-highlight::before {
  content: "";
  position: absolute;
  width: 100%;
  right: 0;
  height: 15%;
  bottom: 3px;
  z-index: -1;
}

.about-highlight:hover::after,
.about-highlight:hover::before {
  display: block;
  width: 100%;
  height: 50%;
  left: 0;
  bottom: 3px;
}

.about-anim::before {
  -webkit-transition: height 0.6s cubic-bezier(0.51, 0.18, 0, 0.88) 0.1s;
  background: #fce995;
  transition: height 0.6s cubic-bezier(0.51, 0.18, 0, 0.88) 0.1s;
  background: #fce995;
}

.about-anim::after {
  /* transition: height 0.2s cubic-bezier(0.29, 0.18, 0.26, 0.83); */
  -webkit-transition: height 0.4s cubic-bezier(0.29, 0.18, 0.26, 0.83);
  background: #ffe880;
  transition: height 0.4s cubic-bezier(0.29, 0.18, 0.26, 0.83);
  background: #ffe880;
}

.rockets-anim::before {
  -webkit-transition: height 0.6s cubic-bezier(0.51, 0.18, 0, 0.88) 0.1s;
  background: rgb(238, 125, 131);
  transition: height 0.6s cubic-bezier(0.51, 0.18, 0, 0.88) 0.1s;
  background: rgb(238, 125, 131);
}

.rockets-anim::after {
  /* transition: height 0.2s cubic-bezier(0.29, 0.18, 0.26, 0.83); */
  -webkit-transition: height 0.4s cubic-bezier(0.29, 0.18, 0.26, 0.83);
  background: rgb(233, 74, 74);
  transition: height 0.4s cubic-bezier(0.29, 0.18, 0.26, 0.83);
  background: rgb(233, 74, 74);
}

@-webkit-keyframes slideInUp {
  from {
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
    visibility: visible;
  }

  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@keyframes slideInUp {
  from {
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
    visibility: visible;
  }

  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}
